let header = document.querySelector(".header")
let burger = document.querySelector(".burger")
let body = document.querySelector("body")


burger.onclick = function () {
    header.classList.toggle("_open")
}


function Slider() {
    var swiper = new Swiper('.main .swiper-container', {
        slidesPerView: 1,
        spaceBetween: 30,
        loop: true,
        autoplay: {
          delay: 4000,
        },
        pagination: {
            el: '.main .swiper-pagination',
            type: 'fraction',
            clickable: true,
        },
        navigation: {
            nextEl: '.main .swiper-button-next',
            prevEl: '.main .swiper-button-prev',
        },
        on: {
            init: function () {
                setAnimatedSlide(this.activeIndex)
            },
            slideChange: function () {
                setAnimatedSlide(this.activeIndex)
            },
            slideChangeTransitionEnd: function () {
                unsetAnimatedSlides()
            }
        },
    })

    function setAnimatedSlide(index){
        // console.log(swiper.activeIndex)
        let activeSlide = document.querySelector(`.main .swiper-slide:nth-child(${index+1})`)
        // console.log(activeSlide)
        activeSlide.classList.add("_anim")
    }

    function unsetAnimatedSlides() {
        let notActiveSlides = document.querySelectorAll(".main .swiper-slide:not(.swiper-slide-active)")
        notActiveSlides.forEach(el => {
            el.classList.remove("_anim")
        })
    }
}

Slider()


let radios = document.querySelectorAll(".donation__radio input[type='radio']")
let textRadio = document.querySelector(".donation__radio--text input")
console.log(radios)
console.log(textRadio)


textRadio.onclick = function () {
    radios.forEach(el => {
        el.checked = false
        textRadio.classList.add("_active")
    })
}

radios.forEach(el => {
    el.onchange = function(){
        textRadio.classList.remove("_active")
    }
})
